.tree {
    &__node {
        align-items: center;
        display: flex;
        gap: 4px;
        position: relative;

        height: 44px;

        .button,
        button {
            margin: 0;
            margin-left: 4px;
        }

        .node__arrow,
        .details__icon,
        .details__title,
        .node__additional > *:not(.menu),
        .details__description {
            z-index: 1;
            position: relative;
        }

        .node__arrow {
            @include iconUxt-regular(arrowSingleRight);

            color: rgba(var(--color-primary), 1);
            visibility: hidden;

            &:hover,
            &:active {
                color: rgba(var(--color-primary), 1);
            }
        }

        .button.node__arrow,
        .button.node__favorite {
            font-size: $iconSizeM;
            height: 32px;
            padding: 7px 7px;
        }

        .node__details {
            align-items: center;
            color: unset;
            display: flex;
            flex-direction: row;
            gap: 4px;
            margin-left: 4px;
            overflow: hidden;
            text-decoration: none;

            &:visited {
                .details__title {
                    color: rgba(var(--color-font), 1);
                }

                .details__description {
                    color: rgba(var(--color-font-secondary), 1);
                }

            }

            &:hover {
                text-decoration: none;

                .details__icon,
                .details__title,
                .details__description {
                    color: rgba(var(--color-primary-dark), 1);
                }

                &::before {
                    background-color: rgba(var(--color-primary-lighter), .15);
                }

            }

            &:active {
                &::before {
                    background-color: rgba(var(--color-primary-lighter), .4);
                }
            }

            &:focus-visible {
                outline: unset;

                &:before {
                    outline: 2px solid rgba(var(--color-primary-light), 1);
                }
            }

            &::before {
                content: "";
                position: absolute;
                inset: 0;
                transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out;
            }

            .details__icon {
                font-size: $iconSizeL;
            }

            .details__content {
                display: flex;
                flex-direction: column;
                overflow: hidden;

                .details__title,
                .details__description {
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                }

                .details__title {
                    font-size: $ms0-fontSize;
                    line-height: $ms0-lineHeight;
                }

                .details__description {
                    color: rgba(var(--color-font-secondary), 1);
                    font-size: $ms-1-fontSize;
                    line-height: $ms-1-lineHeight;
                }

            }


        }

        .node__details:has(+ .node__additional *:hover) {
            &::before {
                background-color: rgba(var(--color-primary-lighter), .15);
            }
        }

        .node__arrow:hover ~ .node__details::before {
            background-color: rgba(var(--color-primary-lighter), .15);
        }

        .node__additional {
            margin-left: auto;
            margin-right: 4px;
            white-space: nowrap;

            .node__favorite {
                @include iconUxt-regular(star);
                color: rgba(var(--color-font-secondary));

                &:hover {
                    @include iconUxt-filled(star);
                    color: rgba(var(--color-primary));
                }

                &:active {
                    @include iconUxt-filled(star);
                    background-color: rgba(var(--color-primary-lighter));
                    color: rgba(var(--color-primary));

                }


                &.is-selected {
                    @include iconUxt-filled(star);
                    color: rgba(var(--color-warning));

                    &:hover {
                        @include iconUxt-regular(star);
                        background-color: rgba(var(--color-primary-lightest));
                        color: rgba(var(--color-primary));
                    }

                    &:active {
                        @include iconUxt-regular(star);
                        background-color: rgba(var(--color-primary-lighter));
                        color: rgba(var(--color-primary));
                    }
                }
            }

            .menu {
                margin-left: 0;

                button:not(.popover__item),
                .button:not(.popover__item) {
                    background-color: rgba(0, 0, 0, 0);
                    margin: 8px 0;

                    &:hover {
                        background-color: rgba(var(--color-primary-lightest), 1);
                    }

                    &:active {
                        background-color: rgba(var(--color-primary-lighter), 1);
                    }
                }

                &.is-shown {

                    > .button,
                    > button {
                        background-color: rgba(var(--color-primary-lighter), 1);
                    }


                }

                .popover__container {
                    min-width: auto;
                }
            }
        }


        &.is-selected {

            .node__details {
                .details__title {
                    font-weight: bold;
                }

                &::before {
                    background-color: rgba(var(--color-primary-lightest), 1);
                }

                &:hover {
                    &::before {
                        background-color: rgba(var(--color-primary-lighter), .15);
                    }
                }

                &:active {
                    &::before {
                        background-color: rgba(var(--color-primary-lighter), .4);
                    }
                }
            }
        }

        &.is-disabled {
            &::after {
                content: "";
                background-color: rgba(var(--color-interface), 1);
                inset: 0;
                opacity: 0.5;
                position: absolute;
                z-index: 2;
            }

            &:hover {
                &::after {
                    cursor: not-allowed;
                }
            }

            &.is-selected {
                .node__details::after {
                    background-color: unset;
                }
            }

            .node__favorite,
            .node__favorite.is-selected {
                color: rgba(var(--color-font-disabled));
            }

            .node__arrow {
                color: rgba(var(--color-font-disabled), 1);
            }
        }

        &.is-hidden {
            display: none;
        }

        &.is-info,
        &.is-warning,
        &.is-error,
        &.is-success {
            &:before {
                content: '';
                inset: 0;
                position: absolute;
                width: 4px;
                z-index: 1;
            }
        }

        &.is-info:before {
            background-color: rgba(var(--color-info), 1);
        }

        &.is-warning:before {
            background-color: rgba(var(--color-warning), 1);
        }

        &.is-error:before {
            background-color: rgba(var(--color-error), 1);
        }

        &.is-success:before {
            background-color: rgba(var(--color-success), 1);
        }

        &.has-indicator-bold:before {
            width: 8px;
        }

        &.is-collapsed.is-nested {

            .tree__node {
                display: none;
            }

            .node__arrow {
                @include iconUxt-regular(arrowSingleRight);
                color: rgba(var(--color-font), 1);
                visibility: visible;

                &:hover,
                &:active {
                    color: rgba(var(--color-primary), 1);
                }
            }
        }

        &.is-nested {
            .node__arrow {
                @include iconUxt-regular(arrowSingleDown);
                visibility: visible;
            }
        }


        @for $i from 1 through $treeLevelCount {
            &.is-level#{$i} {
                padding-left: $treeLevelSpacing * $i;
            }
        }
    }
}